import "./contact.css";
import Phone from "../../img/phone.png";
import Email from "../../img/email.png";
import Address from "../../img/address.png";
import { useContext, useRef, useState } from "react";
import emailjs from "emailjs-com";
import { ThemeContext } from "../../context";

const Contact = () => {
  const formRef = useRef();
  const [done, setDone] = useState(false)
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_ltvqxtb",
        "template_s141dt8",
        formRef.current,
        "yATG7ScZzksj8P8AS"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true)
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="c">
      <div className="c-bg"></div>
      <div className="c-wrapper">
        <div className="c-left">
          <h1 className="c-title">Discutons de votre projet</h1>
          <div className="c-info">
            <div className="c-info-item">
              <img src={Phone} alt="" className="c-icon" />
              0658976477
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Email} alt="" />
              lahmadizakarya47@gmail.com
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Address} alt="" />
              26  Street malt, paris Victoria 8520 Australia
            </div>
          </div>
        </div>
        <div className="c-right">
          <p className="c-desc">
            <b>Quelle est votre histoire ?</b> Contactez-nous. Toujours disponible
            si le bon projet se présente.
          </p>
          
          <form ref={formRef} onSubmit={handleSubmit}>
              <p>
                <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Name" name="user_name"  required/>
              </p>
              <p>
                <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Subject" name="user_subject" required/>
              </p>
              <p>
                <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Email" name="user_email" required/>
              </p>
              <p>
                <textarea style={{backgroundColor: darkMode && "#333"}} rows="5" placeholder="Message" name="message" required/>
              </p>
              <p>
                <button>Submit</button>
              </p>
            {done && "Merci.."}
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
